.header {
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header__logo-link {
  align-content: center;
  width: 40px;
  height: 40px;
  text-decoration: none;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
  position: relative;
}

.nav__item--last-link {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-content: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

html {
  font-family: Roboto, Arial, sans-serif;
}

body, p, h3 {
  margin: 0;
}

.cards {
  --cards-count: 1;
  grid-template-columns: repeat(var(--cards-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    --cards-count: 2;
  }
}

@media (width >= 768px) {
  .cards {
    --cards-count: 3;
  }
}

@media (width >= 1024px) {
  .cards {
    --cards-count: 4;
  }
}

.card {
  box-sizing: border-box;
  color: #060b35;
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  font-size: 12px;
  font-weight: 400;
}

.card__price-price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
